import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import { HOST_API } from '../../config';
import { API_EVENT } from '../../config';

import axios from '../../utils/axios';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function AlertedTicketList() {
    const { t } = useLocales();
    const { user } = useAuth();
    const [events, setEvents] = useState([]);

    useEffect(() => {
        axios.get(API_EVENT.getAlertedTickets).then((res) => {
            if (res.status === 200) setEvents(res.data.data);
        });
    }, [user]);

    return (
        <Page title="Ticket Alert">
            <PageBanner>
                <div className="container p-4 mb-8">
                    <div className="flex gap-4 w-full mb-8">
                        <Link to="../" className="text-primary cursor-pointer underline">
                            {t('title.menu.profile')}
                        </Link>
                        <div>/</div>
                        <div className="grid grid-cols-2 text-stone-400">
                            <p>{t('title.menu.conversation')}</p>
                        </div>
                    </div>
                    <div className="w-full flex mb-[45px]">
                        <h4 className="text-4xl md:text-[64px]">{t('title.menu.alert')}</h4>
                    </div>
                    <div className="flex flex-col gap-4 w-full container mb-8">
                        <div className="grid grid-cols-1 md:grid-cols-3  gap-4">
                            {events?.map((e, index) => (
                                <GradientBorderWrapper key={index} >
                                    <div className="w-full flex gap-4 p-6">
                                        <div className="avatar">
                                            <div className="rounded-full h-[50px] w-[50px]">
                                                <LazyLoadImage
                                                    src={`${HOST_API}uploads/ticket_avatar/default-ticket.svg`}
                                                    alt=""
                                                />
                                            </div>
                                        </div>
                                        <div className="flex flex-col min-w-[300px]">
                                            <h5 className="text-[19px] capitalize">1 {t("title.ticket")}</h5>
                                            <span className="text-sm capitalize text-stone-400">{e.name}</span>
                                            <div className="text-sm text-stone-400 mt-2">
                                                {t("title.ticket")} LOTE 4
                                            </div>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                            ))}
                        </div>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
